<template>
  <div class="row p-4">
    <div class="col-md-4">
      <div class="card bg-my-blue text-white">
        <div class="card-body">
          <h4><font-awesome-icon icon="folder-open"></font-awesome-icon> Total Projects: {{projectNumber}}</h4>
        </div>
      </div>  
    </div>
    <div class="col-md-4">
      <div class="card bg-my-blue text-white">
        <div class="card-body">
          <h4><font-awesome-icon icon="users"></font-awesome-icon> Total Users: {{userNumber}}</h4>
        </div>
      </div>
    </div>
    <div class="col-md-4">
      <div class="card bg-my-blue text-white">
        <div class="card-body">
          <h4><font-awesome-icon :icon="['fab', 'stack-overflow']" style="font-size: 30"></font-awesome-icon> Waitlist: {{waitlistNumber}}</h4>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Axios from 'axios'

export default {
  data() {
    return {
      date: null,
      projectNumber: null,
      userNumber: null,
      waitlistNumber: null
    }
  },
  computed: {
    idToken() {
      return this.$store.state.idToken
    }
  },
  methods: {
    getProjectNumber() {
      Axios.get(`/projects.json?auth=${this.idToken}`)
        .then(res => {
          this.projectNumber = Object.keys(res.data).length
        })
    },
    getUserNumber() {
      Axios.get(`/users.json?auth=${this.idToken}`)
        .then(res => {
          this.userNumber = Object.keys(res.data).length
        })
    },
    getWaitlistNumber() {
      Axios.get(`/waitlist.json`)
        .then(res => {
          this.waitlistNumber = Object.keys(res.data).length
        })
    }
  },
  created() {
    this.getProjectNumber()
    this.getUserNumber()
    const today = new Date()
    this.date = `${String(today.getFullYear())}-${String(today.getMonth() + 1).padStart(2, "0")}-${String(today.getDate()).padStart(2, "0")}`
    this.getWaitlistNumber()
  }
}
</script>

