<template>
  <div>
    <app-header></app-header>
      <div class="container">
        <div class="row">
          <div class="col-lg-6 offset-lg-3">
            <form class="pt-3">
              <input type="date" class="form-control" :max="tomorrow" v-model="date" @change="changeDate">
            </form>
          </div>
          <div class="col-lg-6" v-for="(records, meal_type, index) in diet">
            <div class="card mt-3">
              <div class="card-header bg-info pb-2">
                {{meal_type}}
                <div class="float-right text-white d-inline-block">
                  <font-awesome-icon icon="plus" class="clickable-fa" @click="addRecord[meal_type] = true"></font-awesome-icon>
                </div>
              </div>
              <app-edit-record 
                v-if="addRecord[meal_type]" 
                :meal_type="meal_type"
                :userId="userId"
                @readyToSubmit="saveRecord($event)"
                @cancelSubmit="addRecord[meal_type] = false">
              </app-edit-record>
              <ul class="list-group">
                <li class="list-group-item" v-for="(record, record_id) in records">
                  {{record.item}}, {{record.quantity}} {{record.unit}}
                  <div class="d-inline-block float-right">
                    <font-awesome-icon icon="edit" class="clickable-fa pr-2" @click="showEditRecord(record_id)"></font-awesome-icon>
                  </div>
                  <app-edit-record 
                    v-if="editRecord[record_id]"
                    :meal_type="meal_type"
                    :userId="userId"
                    :item="record.item"
                    :quantity="record.quantity"
                    :unit="record.unit"
                    :remove_record="(true)"
                    @readyToSubmit="updateRecord(record_id, $event)"
                    @cancelSubmit="hideEditRecord(record_id)"
                    @readyToDelete="deleteRecord(record_id, meal_type)">
                  </app-edit-record>
                </li>
              </ul>
            </div>
          </div>
        </div>
      </div>
      <app-footer></app-footer>
  </div>
</template>

<script>
import { mapState } from 'vuex';
import axios from 'axios';
import EditRecord from "./EditRecord.vue"
import Header from "../navbar/Header.vue"
import Footer from "../navbar/Footer.vue"

export default {
  data: function() {
    return {
      diet: {
        breakfest: {},
        lunch: {},
        dinner: {},
        snacks: {}
      },
      addRecord: {
        breakfest: false,
        lunch: false,
        dinner: false,
        snacks: false
      },
      editRecord: {},
      date: null,
      tomorrow: null
    }
  },
  computed: {
    userId () {
      return this.$store.state.userId
    },
    // date () {
    //   return this.$store.state.date
    // },
    idToken () {
      return this.$store.state.idToken
    }
  },
  methods: {
    saveRecord(data) {
      const meal_type = data.meal_type
      axios.post(`diet/${this.userId}/${this.date}.json?auth=${this.idToken}`, data)
        .then(res => {
          this.$set(this.diet[meal_type], res.data.name, data)
          this.diet[meal_type][res.data.name] = data
        });
      this.addRecord[meal_type] = false
    },
    updateRecord(record_id, data) {
      const meal_type = data.meal_type
      axios.put(`diet/${this.userId}/${this.date}/${record_id}.json?auth=${this.idToken}`, data)
      this.$set(this.diet[meal_type], record_id, data)
      this.editRecord[record_id] = false
    },
    deleteRecord(record_id, meal_type){
      axios.delete(`diet/${this.userId}/${this.date}/${record_id}.json?auth=${this.idToken}`)
      this.$delete(this.diet[meal_type], record_id)
    },
    fetchData() {
      axios.get(`/diet/${this.userId}/${this.date}.json?auth=${this.idToken}`)
        .then(res => {
          const data = res.data;
          const resultArray = {
            breakfest: {},
            lunch: {},
            dinner: {},
            snacks: {}
          };
          for (let key in data) {
            resultArray[data[key].meal_type][key] = data[key]
          };
          this.diet = resultArray
        })
    },
    showEditRecord(record_id) {
      this.$set(this.editRecord, record_id, true)
    },
    hideEditRecord(record_id) {
      this.$set(this.editRecord, record_id, false)
    },
    changeDate() {
      this.$store.commit('setDate', this.date)
      this.fetchData()
    }
  },
  created() {
    let date = new Date()
    this.date = `${String(date.getFullYear())}-${String(date.getMonth() + 1).padStart(2, "0")}-${String(date.getDate()).padStart(2, "0")}`
    this.$store.commit('setDate', this.date)
    this.fetchData()
    this.tomorrow = `${String(date.getFullYear())}-${String(date.getMonth() + 1).padStart(2, "0")}-${String(date.getDate() + 1).padStart(2, "0")}`
    console.log(this.tomorrow)
  },
  components: {
    appEditRecord: EditRecord,
    appHeader: Header,
    appFooter: Footer
  }
}
</script>

<style>
  .clickable-fa {
    cursor: pointer;
  }
</style>
