<template> 
  <transition name="modal">
    <div class="modal modal-mask" style="display: block">
      <div class="modal-dialog" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <h4 class="modal-title">
              <slot></slot>
            </h4>
          </div>
            <div class="modal-body" v-if="textInput">
              <form>
                <label class="d-block"><slot name="inputMessage"></slot></label>
                <input type="text" v-model="inputData">
              </form>
              {{ inputData }}
            </div>
          <div class="modal-footer">
            <button 
              class="btn" 
              :class="{'btn-primary': type === 'submit', 'btn-danger': type === 'delete'}"
              @click.prevent="confirm">
                {{ type }}
              </button>
            <button class="btn btn-secondary" @click.prevent="cancel">cancel</button>
          </div>
        </div>
      </div>
    </div>
  </transition> 
</template>

<script>
export default {
  data(){
    return {
      inputData: null
    }
  },
  props: {
    type: {
      type: String,
      required: true
    }, 
    editState: {
      type: Boolean,
      default: false
    },
    textInput: {
      type: Boolean,
      default: false
    }
  },
  methods: {
    confirm() {
      if(this.type === "delete") {
        this.$emit('deleteConfirmed')
      } else if(this.editState) {
        this.$emit('editConfirmed')
      } else {
        this.$emit('addConfirmed', this.inputData)
      }
    },
    cancel() {
      this.$emit('modalCanceled')
    }
  }
}
</script>

<style>
.modal-mask {
  position: fixed;
  z-index: 9998;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, .5);
  display: table;
  transition: opacity .3s ease;
}
</style>