<template>
  <div>
    <app-header></app-header>
    <div class="row pt-4">
      <div class="col-lg-4 offset-lg-4 col-md-6 offset-md-3 col-sm-8 offset-sm-2">
        <div class="card">
          <div class="card-header text-center bg-primary text-white">
            <h4>Sign In</h4>
          </div>
          <div class="alert alert-danger m-3" v-if="passwordError">
            Email or password not matching with the records. Please try again.
          </div>
          <form id="signin" class="p-4" @submit.prevent="loginSubmit">
            <div class="form-group py-2" :class="{ invalid: $v.email.$error }">
              <label>Email Address</label>
              <input 
                type="text" 
                class="form-control" 
                placeholder="email" 
                @blur="$v.email.$touch()"
                v-model="email">
            </div>
            <div class="form-group py-2">
              <label>Password</label>
              <input type="password" class="form-control" placeholder="password" v-model="password">
            </div>
            <div class="pt-2 pb-4">
              <button class="btn btn-primary btn-block" type="submit" :disabled="$v.$invalid">Login</button>
            </div>
          </form>
        </div>
      </div>
    </div>
    <app-footer></app-footer>
  </div>
</template>

<script>
import Header from '../navbar/Header.vue'
import Footer from '../navbar/Footer.vue'
import { required, email } from 'vuelidate/lib/validators'

export default {
  data() {
    return {
      email: '',
      password: ''
    }
  },
  computed: {
    passwordError(){
      return this.$store.state.passwordError
    }
  },
  components: {
    appHeader: Header,
    appFooter: Footer
  },
  validations: {
    email: {
      required,
      email
    }
  },
  methods: {
    loginSubmit() {
      const formData = {
        email: this.email,
        password: this.password,
      }
      this.$store.dispatch('login', {
        email: formData.email, 
        password: formData.password
      })
    }
  }
}
</script>

<style>
.invalid input {
  border: 1px solid red;
  background-color: #ffc9aa
}

.invalid input {
  color: red;
}
</style>