<template>
  <div class="card">
    <div class="card-body">
      <table class="table table-hover">
        <thead>
          <tr>
            <th scope="col"></th>
            <th scope="col">Project Name</th>
            <th scope="col">Coordinator</th>
            <th scope="col">Email</th>
            <th scope="col" class="text-center">
              <font-awesome-icon icon="plus" class="clickable-fa" @click.prevent="addProject = true"></font-awesome-icon>
            </th>
          </tr>
        </thead>
        <tbody v-for="(projectId, ind) in projectIds">
          <app-edit-record
            v-if="addProject && ind === 0"
            :recordData="emptyProject"
            :editState="false"
            :recordTemplete="projectTemplete"
            @showSubmitModal="showSubmitModal($event)"
            @editCanceled="addProject = false"
          ></app-edit-record>
          <tr v-if="editProject !== ind">
            <th scope="row">{{ ind + 1 }}</th>
            <td>{{ projects[projectId].study_name }}</td>
            <td>{{ projects[projectId].coordinator }}</td>
            <td>{{ projects[projectId].coordinator_email }}</td>
            <td class="text-center">
              <font-awesome-icon icon="pencil-alt" class="clickable-fa" @click.prevent="editProject = ind"></font-awesome-icon>
            </td>
          </tr>
          <app-edit-record
            v-if="editProject === ind"
            :recordData="projects[projectId]"
            :editState="true"
            :recordTemplete="projectTemplete"
            @showSubmitModal="showSubmitModal($event, projectId)"
            @showDeleteModal="showDeleteModal(projectId)"
            @editCanceled="editProject = null"
          ></app-edit-record>
        </tbody>
      </table>
      <app-modal 
        v-if="submitModal"
        type="submit"
        :editState="editState" 
        @addConfirmed="submitNewProject"
        @editConfirmed="updateProject"
        @modalCanceled="cancelEdit">
        Sure to submit this project?
      </app-modal>
      <app-modal 
        v-if="deleteModal" 
        type="delete"
        @deleteConfirmed="deleteProject"
        @modalCanceled="cancelEdit">
        Sure to delete this project?  
      </app-modal>
    </div>
  </div>
</template>

<script>
import Axios from 'axios'
import EditRecord from './EditRecord.vue'
import Modal from './modal.vue'

export default {
  data() {
    return {
      projects: {},
      projectIds: [],
      editState: null,
      addProject: false,
      editProject: null,
      projectTemplete: [
        {study_name: "text"},
        {coordinator: "text"},
        {coordinator_email: "email"}
      ],
      projectOnStack: {},
      keyOnStack: null,
      submitModal: false,
      deleteModal: false
    }
  },
  computed: {
    idToken () {
      return this.$store.state.idToken
    },
    emptyProject() {
      const data = {}
      this.projectTemplete.forEach((item) => {
        for (let key in item) {
          data[key] = null
        }
      })
      return data
    }
  },
  components: {
    appEditRecord: EditRecord,
    appModal: Modal
  },
  methods: {
    indexProjects() {
      const projectIds = []
      for (let key in this.projects) {
        projectIds.push(key)
      }
      this.projectIds = projectIds
    },
    showSubmitModal(data, key) {
      this.projectOnStack = data.data
      this.editState = data.editState
      if(key) {
        this.keyOnStack = key
      }
      this.submitModal = true
    },
    showDeleteModal(key) {
      this.keyOnStack = key
      this.deleteModal = true
    },
    cancelEdit() {
      this.addProject = false
      this.editProject = null
      this.submitModal = false
      this.deleteModal = false
    },
    submitNewProject(){
      this.addProject = false
      this.submitModal = false
      Axios.post(`/projects.json?auth=${this.idToken}`, this.projectOnStack)
        .then(res => {
          console.log(res.data)
          this.$set(this.projects, res.data.name, this.projectOnStack)
          this.indexProjects()
          this.projectOnStack = {}
          this.editState = null
        })
        .catch(err => console.log(err))
    },
    updateProject() {
      this.editProject = false
      this.submitModal = false
      Axios.put(`/projects/${this.keyOnStack}.json?auth=${this.idToken}`, this.projectOnStack)
        .then(res => {
          this.$set(this.projects, this.keyOnStack, this.projectOnStack)
          this.projectOnStack = {}
          this.keyOnStack = null
          this.editState = null
        })
    },
    deleteProject(){
      this.editProject = null
      this.deleteModal = false
      Axios.delete(`/projects/${this.keyOnStack}.json?auth=${this.idToken}`)
        .then(res => {
          this.$delete(this.projects, this.keyOnStack)
          this.indexProjects()
          this.keyOnStack = null
        })
    },
    fetchProjects () {
      Axios.get(`/projects.json?auth=${this.idToken}`)
        .then(res => {
          this.projects = res.data
          this.indexProjects()
        })
    }
  },
  created() {
    this.fetchProjects()
  }
}
</script>

<style scoped>
  .clickable-fa {
    cursor: pointer;
  }
  .card {
    margin-top: 1rem;
    padding-top: 0.5rem;
    overflow-x: scroll;
    border-top: solid 3px rgb(44, 116, 250)
  }
  .table-hover tbody tr:hover td, .table-hover tbody tr:hover th {
    background-color: #a3cfec44;
  }
</style>
