<template>
  <tr>
    <th></th>
    <td v-for="(key, ind) in order">
      <input 
        class="form-control" 
        :type="types[ind]" 
        v-model="record[key]">
    </td>
    <td class="text-center">
      <div class="btn-group">
        <button 
          class="btn btn-primary btn-sm"
          @click.prevent="showSubmitModal">save</button>
        <button 
          class="btn btn-danger btn-sm"
          v-if="editState"
          @click.prevent="showDeleteModal">delete</button>
        <button 
          class="btn btn-secondary btn-sm"
          @click.prevent="cancelEdit">cancel</button>
      </div>
    </td>
  </tr>
</template>


<script>
import Modal from './modal.vue'

export default {
  props: ["recordData", "recordTemplete", "editState"],
  data() {
    return {
      record: {},
      order: [],
      types: {}
    }
  },
  methods: {
    showSubmitModal() {
      this.$emit('showSubmitModal', {
        editState: this.editState,
        data: this.record
      })
    },
    showDeleteModal() {
      this.$emit('showDeleteModal')
    },
    cancelEdit() {
      this.$emit('editCanceled')
    }
  },
  created() {
    for (let key in this.recordData){
      this.$set(this.record, key, this.recordData[key])
    }
    this.recordTemplete.forEach((item, ind) => {
      for (let key in item) {
        this.$set(this.order, ind, key)
        this.$set(this.types, key, item[key])
      }
    }) 
  }
}
</script>

<style scoped>
  .table-hover tbody tr:hover td, .table-hover tbody tr:hover th {
    background-color: #a3cfec44;
  }
</style>
