<template>
  <div class="pb-3">
    <hr>
    <form class="px-3">
      <div class="form-row">
        <div class="form-group col-md-6">
          <label>Item Name</label>
          <input type="text" class="form-control" placeholder="food item" v-model="item">
        </div>
        <div class="form-group col-md-3">
          <label>Quantity</label>
          <input type="number" class="form-control" placeholder="0" v-model="quantity">
        </div>
        <div class="form-group col-md-3">
          <label>Unit</label>
          <input type="text" class="form-control" placeholder="one/lb/oz" v-model="unit">
        </div>
        <div class="py-1" :class="{'col-md-4': remove_record, 'col-md-6': !remove_record}">
          <button 
            class="btn btn-primary btn-block" 
            @click.prevent="submitRecord">Submit</button>
        </div>
        <div class="col-md-4 py-1" v-if="remove_record" >
          <button 
            class="btn btn-danger btn-block" 
            @click="deleteConfirmModal">Remove</button>
        </div>
        <div class="py-1" :class="{'col-md-4': remove_record, 'col-md-6': !remove_record}">
          <button 
            class="btn btn-warning btn-block" 
            @click.prevent="cancelSubmit">Cancel</button>
        </div>
      </div>
    </form>
    <app-delete-modal 
      v-if="showModal" 
      @confirmDelete="deleteSubmit"
      @cancelDelete="cancelDelete"></app-delete-modal>
  </div>
</template>

<script>
import DeleteModal from './DeleteModal'
export default {
  data() {
    return {
      showModal: false
    }
  },
  props: {
    meal_type: String, 
    userName: String, 
    item: String, 
    quantity: String,
    unit: String,
    remove_record: Boolean
  },
  components: {
    appDeleteModal: DeleteModal
  },
  methods: {
    submitRecord() {
      const data = {
        userName: this.userName,
        meal_type: this.meal_type, 
        item: this.item, 
        quantity: this.quantity, 
        unit: this.unit
      }
      this.$emit("readyToSubmit", data)
    },
    cancelSubmit() {
      this.$emit("cancelSubmit")
    },
    deleteConfirmModal(){
      this.showModal = true;
    },
    deleteSubmit() {
      this.$emit("readyToDelete")
      this.showModal = false
    },
    cancelDelete() {
      this.showModal = false
    }
  }
}
</script>
