<template>
  <div class="row">
    <div class="card m-3" style="width: 100%">
      <div class="card-body">
        <div class="containter">
          <form>
            <div class="form-row">
              <div class="form-group col-md-4">
                <label>Project</label>
                <select 
                  class="custom-select" 
                  v-model="selectedProject"
                  @change="fetchUsers">
                  <option 
                    v-for="(project, ind) in projects" 
                    :value="project">{{project.study_name}}</option>
                </select>
              </div>
              <div class="form-group col-md-4">
                <label>Subject</label>
                <select 
                  class="custom-select" 
                  v-model="selectedSubject"
                  @change="fetchDietRecords">
                  <option 
                    v-for="subject in subjects"
                    :value="subject">{{subject.info}}</option>
                </select>
              </div>
              <div class="form-group col-md-4">
                <label>Date</label>
                <input 
                  type="date" 
                  class="form-control" 
                  v-model="date"
                  @change="fetchDietRecords">
              </div>
            </div>
          </form>
        </div>
      </div>
    </div>
    <div class="card m-3" style="width: 100%">
      <div class="card-body">
        <table class="table table-hover">
          <thead>
            <tr>
              <th scope="col"></th>
              <th scope="col">Meal Type</th>
              <th scope="col">Food Item</th>
              <th scope="col">Quantity</th>
              <th scope="col">Unit</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="(dietIndex, ind) in dietIndices">
              <th>{{ ind + 1 }}</th>
              <td>{{ dietRecords[dietIndex].meal_type }}</td>
              <td>{{ dietRecords[dietIndex].item }}</td>
              <td>{{ dietRecords[dietIndex].quantity }}</td>
              <td>{{ dietRecords[dietIndex].unit }}</td>
            </tr>
          </tbody>
        </table>
        <app-pagination
          :pages="pages"
          :currentPage="currentPage"
          @goToPage="currentPage = $event"
          @goPrevPage="currentPage -= 1"
          @goNextPage="currentPage += 1"
        ></app-pagination>
      </div>
    </div>
  </div>
</template>

<script>
import Axios from 'axios'
import Pagination from './Pagination.vue'

export default {
  data() {
    return{
      projects: [],
      subjects: [],
      dietRecords: {},
      dietIndices: [],
      date: null,
      selectedProject: {},
      selectedSubject: {},
      pages: null,
      currentPage: 1
    }
  },
  computed: {
    idToken() {
      return this.$store.state.idToken
    }
  },
  methods: {
    getDietIndex(){
      const dietIndices = []
      for(let key in this.dietRecords){
        dietIndices.push(key)
      }
      this.dietIndices = dietIndices
      this.pages = Math.ceil(this.dietIndices.length / 10)
      this.currentPage = 1
    },
    fetchProjects() {
       Axios.get(`/projects.json?auth=${this.idToken}`)
        .then(res => {
          const projects = []
          for (let key in res.data){
            projects.push({
              id: key,
              study_name: res.data[key].study_name
            })
          }
          this.projects = projects
          this.selectedProject = projects[0]
          this.fetchUsers()
        })
        .catch(err => console.log(err))
    },
    fetchUsers() {
      Axios.get(`/users.json?orderBy="study_name"&equalTo="${this.selectedProject.study_name}"&auth=${this.idToken}`)
        .then(res => {
          const users = []
          for (let userId in res.data) {
            const subject = res.data[userId]
            users.push({
              userId: userId,
              info: `${subject.subject_id} | ${subject.first_name} ${subject.last_name}`
            })
          }
          this.subjects = users
          this.selectedSubject = users[0]
          this.fetchDietRecords()
        })
          .catch(err => console.log(err))
    },
    fetchDietRecords() {
      Axios.get(`/diet/${this.selectedSubject.userId}/${this.date}.json?auth=${this.idToken}`)
        .then(res => {
          this.dietRecords = res.data
          this.getDietIndex()
        })
    }
  },
  components: {
    appPagination: Pagination
  },
  created() {
    this.fetchProjects()
    const today = new Date()
    this.date = `${String(today.getFullYear())}-${String(today.getMonth() + 1).padStart(2, "0")}-${String(today.getDate()).padStart(2, "0")}`
    
  }
}
</script>

<style scoped>
  .table-hover tbody tr:hover td, .table-hover tbody tr:hover th {
    background-color: #a3cfec44;
  }
  .card{
    overflow-x: scroll;
    margin-top: 1rem;
    padding-top: 0.5rem;
    overflow-x: scroll;
    border-top: solid 3px rgb(44, 116, 250)
  }
</style>
