<template>
  <div class="card">
    <div class="card-body">
      <table class="table table-hover">
        <thead>
          <tr>
            <th scope="col"></th>
            <th scope="col">
              Last Name
              <input type="text" class="form-control input-sm"
              @input="filterTable('last_name', $event)">
            </th>
            <th scope="col">
              First Name
              <input type="text" class="form-control"
              @input="filterTable('first_name', $event)">
            </th>
            <th scope="col">
              Email
              <input type="text" class="form-control"
              @input="filterTable('email', $event)">
            </th>
            <th scope="col">
              Study Name
              <input type="text" class="form-control"
              @input="filterTable('study_name', $event)">
            </th>
            <th scope="col">
              Subject ID
              <input type="text" class="form-control"
              @input="filterTable('subject_id', $event)">
            </th>
            <th scope="col" class="text-center">
              <font-awesome-icon icon="plus" class="clickable-fa" @click.prevent="addUser = true"></font-awesome-icon>
            </th>
          </tr>
        </thead>
        <tbody v-for="(userId, ind) in userIds.slice((currentPage - 1) * 10, currentPage * 10 )">
          <app-edit-record
            v-if="addUser && ind === 0"
            :recordData="emptyUser"
            :editState="false"
            :recordTemplete="userTemplete"
            @showSubmitModal="showSubmitModal($event)"
            @editCanceled="addUser = false"
          ></app-edit-record>
          <tr v-if="editUser !== ind">
            <th scope="row">{{ ind + 1 }}</th>
            <td>{{ users[userId].last_name }}</td>
            <td>{{ users[userId].first_name }}</td>
            <td>{{ users[userId].email }}</td>
            <td>{{ users[userId].study_name }}</td>
            <td>{{ users[userId].subject_id }}</td>
            <td class="text-center" style="white-space: nowrap;">
              <font-awesome-icon icon="pencil-alt" class="clickable-fa" @click.prevent="editUser = ind"></font-awesome-icon>
              <font-awesome-icon icon="download" class="clickable-fa" @click.prevent="downloadUserRecords"></font-awesome-icon>
            </td>
          </tr>
          <app-edit-record
            v-if="editUser === ind"
            :recordData="users[userId]"
            :editState="true"
            :recordTemplete="userTemplete"
            @showSubmitModal="showSubmitModal($event, userId)"
            @showDeleteModal="showDeleteModal(userId)"
            @editCanceled="editUser = null"
          ></app-edit-record>
        </tbody>
      </table>
      <app-pagination
        :pages="pages"
        :currentPage="currentPage"
        @goToPage="currentPage = $event"
        @goPrevPage="currentPage -= 1"
        @goNextPage="currentPage += 1"
        ></app-pagination>
      <app-modal 
        v-if="submitModal"
        type="submit" 
        :editState="editState"
        @addConfirmed="submitNewUser"
        @editConfirmed="updateUser"
        @modalCanceled="cancelEdit">
        Sure to submit this user?
      </app-modal>
      <app-modal 
        v-if="deleteModal" 
        type="delete"
        @deleteConfirmed="deleteUser"
        @modalCanceled="cancelEdit">
        Sure to delete this user?  
      </app-modal>
    </div>
  </div>
</template>

<script>
import Axios from 'axios'
import EditRecord from './EditRecord.vue'
import Modal from './modal.vue'
import Pagination from './Pagination.vue'

export default {
  data() {
    return {
      users: {},
      userIds: [],
      editState: null,
      addUser: false,
      editUser: null,
      userTemplete: [
        {last_name: "text"},
        {first_name: "text"},
        {email: "email"},
        {study_name: "text"},
        {subject_id: "text"}
      ],
      userOnStack: {},
      keyOnStack: null,
      submitModal: false,
      deleteModal: false,
      pages: null,
      currentPage: null
    }
  },
  computed: {
    idToken () {
      return this.$store.state.idToken
    },
    emptyUser() {
      const data = {}
      this.userTemplete.forEach((item) => {
        for (let key in item) {
          data[key] = null
        }
      })
      return data
    }
  },
  components: {
    appEditRecord: EditRecord,
    appModal: Modal,
    appPagination: Pagination
  },
  methods: {
    indexUsers() {
      const userIds = []
      for (let key in this.users) {
        userIds.push(key)
      }
      this.userIds = userIds
      this.pages = Math.ceil(this.userIds.length / 10)
      this.currentPage = 1
    },
    filterTable(attr, event){
      const newIds = []
      for(let key in this.users){
        const column = this.users[key][attr]
        if (column !== undefined && column.search(event.target.value) !== -1) {
          newIds.push(key)
        }
      }
      this.userIds = newIds
    },
    showSubmitModal(data, key) {
      this.userOnStack = data.data
      this.editState = data.editState
      this.submitModal = true
      if(key) {
        this.keyOnStack = key
      }
    },
    showDeleteModal(key) {
      this.keyOnStack = key
      this.deleteModal = true
    },
    cancelEdit() {
      this.addUser = false
      this.editUser = null
      this.submitModal = false
      this.deleteModal = false
    },
    submitNewUser(){
      this.addUser = false
      this.submitModal = false
      this.$set(this.userOnStack, "user_type", "user")
      console.log(this.userOnStack)
      Axios.post(`/users.json?auth=${this.idToken}`, this.userOnStack)
        .then(res => {
          this.$set(this.users, res.data.name, this.userOnStack)
          this.indexUsers()
          this.userOnStack = {}
          this.editState = null
        })
        .catch(err => console.log(err))
    },
    updateUser() {
      this.editUser = false
      this.submitModal = false
      this.$set(this.userOnStack, "user_type", "user")
      Axios.put(`/users/${this.keyOnStack}.json?auth=${this.idToken}`, this.userOnStack)
        .then(res => {
          console.log(res.data)
          this.$set(this.users, this.keyOnStack, this.userOnStack)
          this.userOnStack = {}
          this.keyOnStack = null
          this.editState = null
        })
    },
    deleteUser(){
      this.editUser = null
      this.deleteModal = false
      Axios.delete(`/users/${this.keyOnStack}.json?auth=${this.idToken}`)
        .then(res => {
          this.$delete(this.users, this.keyOnStack)
          this.indexUsers()
          this.keyOnStack = null
          this.editState = null
        })
    },
    fetchUsers () {
      Axios.get(`/users.json?auth=${this.idToken}`)
        .then(res => {
          for(let key in res.data){
            if(res.data[key].user_type === 'user') {
              this.$set(this.users, key, res.data[key])
            }
          }
          this.indexUsers()
        })
    }
  },
  created() {
    this.fetchUsers()
  }
}
</script>

<style scoped>
  .clickable-fa {
    cursor: pointer;
    margin-left: 5px;
    margin-right: 5px;
  }
  input {
    height: 30px;
    padding: 5px 10px;
    font-size: 12px;
    line-height: 1.5;
    border-radius: 3px;
  }
  .card {
    margin-top: 1rem;
    padding-top: 0.5rem;
    overflow-x: scroll;
    border-top: solid 3px rgb(44, 116, 250)
  }
  .table-hover tbody tr:hover td, .table-hover tbody tr:hover th {
    background-color: #a3cfec44;
  }
</style>
