import Vue from 'vue'
import VueRouter from 'vue-router'

import Welcome from './components/welcome/Welcome.vue'
import DietTracker from './components/user/DietTracker.vue'
import Admin from './components/admin/Admin.vue'
import SigninPage from './components/auth/signin.vue'
import SignupPage from './components/auth/signup.vue'
import RegisterPage from './components/auth/register.vue'
import NotFoundPage from './components/welcome/NotFound.vue'

import OverviewPage from './components/admin/overview.vue'
import ProjectsPage from './components/admin/projects.vue'
import UsersPage from './components/admin/users.vue'
import RecordsPage from './components/admin/records.vue'
import WaitlistPage from './components/admin/waitlist.vue'
import store from './store'

Vue.use(VueRouter);

const routes = [
  { path: '', component: Welcome },
  { 
    path: '/user', 
    component: DietTracker,
    beforeEnter (to, from, next) {
      if (store.state.idToken) {
        next()
      } else {
        next('/signin')
      }
    }
  },
  { 
    path: '/admin', 
    component: Admin,
    redirect: '/admin/overview',
    children: [
      {
        path: 'overview',
        name: "OverviewPage",
        component: OverviewPage
      },
      {
        path: 'projects',
        name: "ProjectsPage",
        component: ProjectsPage
      },
      {
        path: 'users',
        name: "UsersPage",
        component: UsersPage
      },
      {
        path: "records",
        name: "RecordsPage",
        component: RecordsPage
      },
      {
        path: "waitlist",
        name: "WaitlistPage",
        component: WaitlistPage
      }
    ],
    beforeEnter (to, from, next) {
      if (store.state.idToken) {
        next()
      } else {
        next('/signin')
      }
    }
  },
  { path: '/signin', component: SigninPage },
  { path: '/signup', component: SignupPage },
  { path: '/register', component: RegisterPage },
  { path: '*', component: NotFoundPage }
]

export default new VueRouter({
  routes
})