<template>
  <nav aria-label="Pages" class="ml-auto">
    <ul class="pagination justify-content-end">
      <li class="page-item" :class="{disabled: currentPage === 1 || pages === 0}">
        <a class="page-link" 
          href="#" 
          aria-label="Previous"
          @click.prevent="goPrevPage">
          <span aria-hidden="true">&laquo;</span>
          <span class="sr-only">Previous</span>
        </a>
      </li>
      <li v-for="page in pages" 
        class="page-item"
        :class="{active: page === currentPage}">
        <a class="page-link" href="#" @click.prevent="goToPage(page)">
          {{ page }}
        </a>
      </li>
      <li class="page-item" :class="{disabled: currentPage === pages || pages === 0}">
        <a 
          class="page-link" 
          href="#" 
          aria-label="Next"
          @click.prevent="goNextPage">
          <span aria-hidden="true">&raquo;</span>
          <span class="sr-only">Next</span>
        </a>
      </li>
    </ul>
  </nav>
</template>

<script>
export default {
  props: ["pages", "currentPage"],
  methods: {
    goToPage(page){
      this.$emit("goToPage", page)
    },
    goPrevPage(){
      this.$emit("goPrevPage")
    },
    goNextPage(){
      this.$emit("goNextPage")
    }
  }
}
</script>
