<template>
  <div>
    <app-header></app-header>    
    <div class="row pt-4">
      <div class="col-lg-6 offset-lg-3 col-md-8 offset-md-2 col-sm-8 offset-sm-2">
        <div class="card">
          <div class="card-header text-center bg-primary text-white">
            <h4>Register</h4>
          </div>
          <div class="card-body">

            <div class="alert alert-danger" v-if="emailExistError">
              A registration request was already submitted before. Please don't submit repeatly.
            </div>
            <div class="alert alert-success" v-if="requestRecieved">
              Your info was submited successfully. Thank you!
            </div>

            <form @submit.prevent="registerUser">
              <div class="form-row">
                <div class="form-group col-md-6" :class="{invalid: $v.first_name.$error}">
                  <label>First Name</label>
                  <input 
                    type="text" 
                    class="form-control"
                    v-model="first_name"
                    @blur="$v.first_name.$touch()">
                </div>
                <div class="form-group col-md-6" :class="{invalid: $v.last_name.$error}">
                  <label>Last Name</label>
                  <input 
                    type="text" 
                    class="form-control"
                    v-model="last_name"
                    @blur="$v.last_name.$touch()">
                </div>
              </div>
              <div class="form-row">
                <div class="form-group col-md-6" :class="{invalid: $v.study_name.$error}">
                  <label>Which study are you interested in?</label>
                  <input 
                    type="text" 
                    class="form-control"
                    v-model="study_name"
                    @blur="$v.study_name.$touch()">
                </div>
                <div class="form-group col-md-6" :class="{invalid: $v.email.$error}">
                  <label>Email</label>
                  <input 
                    type="email" 
                    class="form-control"
                    v-model="email"
                    @blur="$v.email.$touch()">
                </div>
              </div>
              <div class="form-row">
                <div class="form-group col-md-6" :class="{invalid: $v.password.$error}">
                  <label>Password</label>
                  <input 
                    type="password" 
                    class="form-control"
                    v-model="password"
                    @blur="$v.password.$touch()">
                </div>
                <div class="form-group col-md-6" :class="{invalid: $v.confirmPassword.$error}">
                  <label>Confirm Password</label>
                  <input 
                    type="password" 
                    class="form-control"
                    v-model="confirmPassword"
                    @blur="$v.confirmPassword.$touch()">
                </div>
              </div>
              <hr>
              <div class="col-sm-6 offset-sm-3">
                <button 
                  class="btn btn-primary btn-block" 
                  :disabled="$v.$invalid">Submit</button>
              </div>
            </form>
          </div>
        </div>
      </div>
    </div>
    <app-footer></app-footer>
  </div>
</template>

<script>
import Axios from 'axios'
import Header from '../navbar/Header.vue'
import Footer from '../navbar/Footer.vue'
import { required, email, minLength, sameAs } from 'vuelidate/lib/validators'

export default {
  data() {
    return {
      first_name: null,
      last_name: null,
      study_name: null,
      email: null,
      password: null,
      confirmPassword: null,
      emailExistError: false,
      requestRecieved: false
    }
  },
  validations: {
    first_name: { required },
    last_name: { required },
    study_name: { required },
    email: {
      required: required,
      email: email
    },
    password: {
      required,
      minLen: minLength(6)
    },
    confirmPassword: {
      sameAs: sameAs('password')
    }
  },
  methods: {
    registerUser() {
      Axios.get(`/waitlist.json?orderBy="email"&equalTo="${this.email}"`)
        .then(getRes => {
          if(Object.keys(getRes.data).length !== 0) {
            this.emailExistError = true
            setTimeout(() => {
              this.emailExistError = false
            }, 15000)
            this.cleanInput()
            return
          }
          const registerData = {
            first_name: this.first_name,
            last_name: this.last_name,
            study_name: this.study_name,
            email: this.email,
            password: this.password
          }
          Axios.post('/waitlist.json', registerData)
            .then(postRes => {
              console.log(postRes)
              this.requestRecieved = true
              this.cleanInput()
              setTimeout(() => {
                this.requestRecieved = false
                this.$router.replace("/")
              }, 15000)
            })
        })
        .catch(getErr => console.log(getErr))
    },
    cleanInput() {
      this.first_name = null
      this.last_name = null
      this.study_name = null
      this.email = null
      this.password = null
      this.confirmPassword = null
    }
  },
  components: {
    appHeader: Header,
    appFooter: Footer
  }
}
</script>

<style>
.invalid input {
  border: 1px solid red;
  background-color: #ffc9aa
}

.invalid input {
  color: red;
}
</style>