<template>
  <div>
    <app-header></app-header>
    <div class="row pt-4">
      <div class="col-lg-4 offset-lg-4 col-md-6 offset-md-3 col-sm-8 offset-sm-2">
        <div class="card">
          <div class="card-header text-center bg-primary text-white">
            <h4>Sign Up</h4>
          </div>
          <div class="alert alert-danger mt-3 mx-3" v-if="unauthEmailError">
            Looks like your email address is not autharized. Please contact chhzhu@ucdavis.edu.
          </div>
          <div class="alert alert-danger mt-3 mx-3" v-if="emailExistError">
            Your email already exists. Please try logging in or contact chhzhu@ucdavis.edu
          </div>
          <form id="signin" class="p-4" @submit.prevent="signupSubmit">
            <div class="form-group py-2" :class="{invalid: $v.email.$error}">
              <label>Email Address</label>
              <input 
                type="text" 
                class="form-control" 
                placeholder="email" 
                @blur="$v.email.$touch()"
                v-model="email">
            </div>
            <div class="form-group py-2" :class="{ invalid: $v.password.$error }">
              <label>Password</label>
              <input 
                type="password" 
                class="form-control" 
                placeholder="password" 
                @blur="$v.password.$touch()"
                v-model="password">
            </div>
            <div class="form-group py-2" :class="{ invalid: $v.confirmPassword.$error }">
              <label>Comfirm Password</label>
              <input 
                type="password" 
                class="form-control" 
                placeholder="password" 
                @blur="$v.confirmPassword.$touch()"
                v-model="confirmPassword">
            </div>
            <div class="pt-2 pb-4">
              <button class="btn btn-primary btn-block" type="submit" :disabled="$v.$invalid">Submit</button>
            </div>
          </form>
        </div>
      </div>
    </div>
    <app-footer></app-footer>
  </div>
</template>

<script>
import { required, email, minLength, sameAs } from 'vuelidate/lib/validators'
import Axios from 'axios'
import Header from "../navbar/Header.vue"
import Footer from "../navbar/Footer.vue"

export default {
  data() {
    return {
      email: '',
      password: '',
      userName: '',
      confirmPassword: ''
    }
  },
  computed: {
    unauthEmailError() {
      return this.$store.state.unauthEmailError
    },
    emailExistError() {
      return this.$store.state.emailExistError
    }
  },
  components: {
    appHeader: Header,
    appFooter: Footer
  },
  validations: {
    email: {
      required: required,
      email: email
    },
    password: {
      required,
      minLen: minLength(6)
    },
    confirmPassword: {
      sameAs: sameAs('password')
    }
  },
  methods: {
    signupSubmit () {
      const formData = {
        email: this.email,
        password: this.password,
        confirmPassword: this.confirmPassword
      }
      this.$store.dispatch('signup', {
        email: formData.email, 
        password: formData.password
      })
      this.email = null
      this.password = null
      this.confirmPassword = null
    }
  }
}
</script>

<style>
.invalid input {
  border: 1px solid red;
  background-color: #ffc9aa
}

.invalid input {
  color: red;
}
</style>
