import Vue from 'vue'
import Vuex from 'vuex'
import axios from './axios-auth'
import router from './router'
import globalAxios from 'axios'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    idToken: null,
    localId: null,
    date: null,
    userType: null,
    userId: null,
    unauthEmailError: false,
    emailExistError: false,
    passwordError: false
  },
  mutations: {
    setIdToken (state, idToken){
      state.idToken = idToken
    },
    setUserId (state, userId) {
      state.userId = userId
    },
    setUserType (state, userType) {
      state.userType = userType
    },
    setDate (state, date) {
      state.date = date
    },
    clearAuthData (state) {
      state.idToken = null
      //state.localId = null
      state.userType = null
      state.userId = null
    },
    setUnauthEmailError (state) {
      state.unauthEmailError = true
      setTimeout(() => {
        state.unauthEmailError = false
      }, 15000)
    },
    setEmailExistError (state) {
      state.emailExistError = true
      setTimeout(() => {
        state.emailExistError = false
      }, 15000)
    },
    setPasswordError(state) {
      state.passwordError = true
      setTimeout(() => {
        state.passwordError = false
      }, 15000)
    }
  },
  actions: {
    setLogoutTimer ({commit}, expirationTime) {
      setTimeout(() => {
        commit('clearAuthData')
        router.replace("/")
      }, expirationTime * 1000)
    },
    signup ({commit, dispatch}, authData) {
      globalAxios.get(`/users.json?orderBy="email"&equalTo="${authData.email}"`)
        .then(usersRes => {
          console.log(usersRes.data)
          if(Object.keys(usersRes.data).length === 0){
            commit('setUnauthEmailError')
            return
          }
          const userIds = []
          for(let key in usersRes.data){
            userIds.push(key)
          }
          this.userId = userIds[0]
          // need to deal with the issue when already signed up
          axios.post('/signupNewUser?key=AIzaSyBgfyfLVpm5Xa9lTMesWPLA1Q3_2JJfOdI', {
            email: authData.email,
            password: authData.password,
            returnSecureToken: true
          })
            .then(authRes => {
              console.log(authRes)
              commit('authUser', authRes.data.idToken)
              commit('setUserType', "user")
              const now = new Date()
              const expirationDate = new Date(now + authRes.data.expiresIn * 1000)

              localStorage.setItem('token', authRes.data.idToken)
              localStorage.setItem('expirationDate', expirationDate)
              //localStorage.setItem('localId', res.data.localId)
              localStorage.setItem('userId', this.userId)
              localStorage.setItem('userType', "user")
              dispatch('setLogoutTimer', authRes.data.expiresIn)
            })
              .catch(err => {
                console.log(err.response.status)
                commit('setEmailExistError')
              })
        })
        .catch(err => {
          // show information to contact admin
          console.log(err)
        })
    },
    login ({commit, dispatch}, authData) {
      axios.post('/verifyPassword?key=AIzaSyBgfyfLVpm5Xa9lTMesWPLA1Q3_2JJfOdI', {
        email: authData.email,
        password: authData.password,
        returnSecureToken: true
      })
        .then(res => {
          console.log(res)
          commit('setIdToken', res.data.idToken)
          const now = new Date()
          const expirationDate = new Date(now + res.data.expiresIn * 1000)
          localStorage.setItem('token', res.data.idToken)
          localStorage.setItem('expirationDate', expirationDate)
          dispatch('setLogoutTimer', res.data.expiresIn)
          dispatch('fetchUser', authData.email)
        })
        .catch(err => {
          console.log(err)
          commit('setPasswordError')
        })
    },
    tryAutoLogin ({commit, state}) {
      const token = localStorage.getItem('token')
      if (!token) {
        return
      }
      commit('setIdToken', token)
      const expirationDate = localStorage.getItem('expirationDate')
      const now = new Date()
      if (now >= expirationDate) {
        return
      }
      const userId = localStorage.getItem("userId")
      commit('setUserId', userId)
      const userType = localStorage.getItem("userType")
      commit('setUserType', userType)
      if(state.userType === "user") {
        router.replace('/user')
      } else if (state.userType === "admin") {
        router.replace('/admin')
      }
    },
    logout ({commit}) {
      commit('clearAuthData')
      localStorage.removeItem('expirationDate')
      localStorage.removeItem('token')
      localStorage.removeItem('userId')
      localStorage.removeItem('userType')
      router.replace('/signin')
    },
    fetchUser ({commit, state}, email) {
      globalAxios.get(`/users.json?orderBy="email"&equalTo="${email}"`)
        .then(res => {
          console.log(res)
          const userIds = []
          for (let key in res.data){
            userIds.push(key)
          }
          commit('setUserId', userIds[0])
          localStorage.setItem('userId', userIds[0])
          const userType = res.data[state.userId].user_type
          commit('setUserType', userType)
          localStorage.setItem("userType", userType)
          if(state.userType === "user") {
            router.replace('/user')
          } else if (state.userType === "admin") {
            router.replace('/admin')
          }
        })
    }
  },
  getters: {
    dietRecords (state) {
      return state.dietRecords
    },
    isAuthenticated (state) {
      return state.idToken !== null
    }
  }
})