<template>
  <div class="card">
    <div class="card-body">
      <div class="table table-hover">
        <table class="table">
          <thead>
            <tr>
              <th scope="col"></th>
              <th scope="col">First Name</th>
              <th scope="col">Last Name</th>
              <th scope="col">Interested Study</th>
              <th scope="col">Email</th>
              <th scope="col"></th>
            </tr>
          </thead>
          <tbody v-for="(userId, ind) in waitlistIndices">
            <tr v-if="editUser !== userId">
              <th scope="row">{{ ind + 1 }}</th>
              <td>{{ waitlist[userId].first_name }}</td>
              <td>{{ waitlist[userId].last_name }}</td>
              <td>{{ waitlist[userId].study_name }}</td>
              <td>{{ waitlist[userId].email }}</td>
              <th scope="col" class="text-center">
                <div class="btn-group">
                  <button 
                    class="btn btn-primary btn-sm" 
                    @click.prevent="editUser=userId">edit</button>
                  <button 
                    class="btn btn-success btn-sm"
                    @click.prevent="acceptUserShowModal(userId)">accept</button>
                  <button 
                    class="btn btn-danger btn-sm"
                    @click.prevent="rejectUserShowModal(userId)">reject</button>
                </div>
              </th>
            </tr>
            <app-edit-record
              v-if="editUser === userId"
              :recordData="waitlist[userId]"
              :editState="false"
              :recordTemplete="recordTemplete"
              @showSubmitModal="saveChangeShowModal($event, userId)"
              @editCanceled="editUser = null"
            ></app-edit-record>
          </tbody>
        </table>
        <app-pagination
          :pages="pages"
          :currentPage="currentPage"
          @goToPage="currentPage = $event"
          @goPrevPage="currentPage -= 1"
          @goNextPage="currentPage += 1"
        ></app-pagination>
      </div>
    </div>

    <app-modal
      v-if="submitModal"
      type="submit"
      :editState="true" 
      @editConfirmed="saveChange"
      @modalCanceled="resetStackData">
      Sure to save this change?  
    </app-modal>

    <app-modal
      v-if="acceptModal"
      type="submit"
      :textInput="true"
      @addConfirmed="acceptUser($event)"
      @modalCanceled="resetStackData">
      <template>Accept</template>
      <template v-slot:inputMessage>Assign a subject ID: </template>
    </app-modal>

    <app-modal
      v-if="rejectModal"
      type="delete"
      @deleteConfirmed="removeFromWaitlist"
      @modalCanceled="resetStackData">
      Sure to reject this candidate?
    </app-modal>

  </div>
</template>

<script>
import Axios from 'axios'
import AuthAxios from '../../axios-auth.js'
import EditRecord from './EditRecord.vue'
import Modal from './modal.vue'
import Pagination from './Pagination.vue'

export default {
  data() {
    return {
      waitlist: {},
      waitlistIndices: [],
      editUser: null,
      recordTemplete: [
        { "first_name": "text" },
        { "last_name": "text" },
        { "study_name": "text" },
        { "email": "email" }
      ],
      recordOnStack: {},
      userIdOnStack: null,
      submitModal: false,
      acceptModal: false,
      rejectModal: false,
      pages: null,
      currentPage: 1
    }
  },
  computed: {
    idToken() {
      return this.$store.state.idToken
    }
  },
  methods: {
    fetchWaitlist() {
      Axios.get('/waitlist.json')
        .then(res => {
          this.waitlist = res.data
          this.getWaitlistIndices()
        })
    },
    getWaitlistIndices() {
      const indices = []
      for (let key in this.waitlist) {
        indices.push(key)
      }
      this.waitlistIndices = indices
      this.pages = Math.ceil(this.waitlistIndices.length / 10)
      this.currentPage = 1
    },
    saveChangeShowModal(event, userId) {
      console.log(event.data)
      this.recordOnStack = event.data
      this.userIdOnStack = userId
      this.submitModal = true
    },
    acceptUserShowModal(userId){
      this.userIdOnStack = userId
      this.acceptModal = true
    },
    rejectUserShowModal(userId){
      this.userIdOnStack = userId
      this.rejectModal = true
    },
    saveChange() {
      Axios.put(`/waitlist/${this.userIdOnStack}.json`, this.recordOnStack)
        .then(res => {
          this.$set(this.waitlist, this.userIdOnStack, this.recordOnStack)
          this.resetStackData()
        })
        .catch(err => console.log(err))
    },
    resetStackData() {
      this.recordOnStack = {}
      this.userIdOnStack = null
      this.submitModal = false
      this.acceptModal = false
      this.rejectModal = false
      this.editUser = null
      this.subject_id = null
    },
    acceptUser(event) {
      const userData = this.waitlist[this.userIdOnStack]
      const subjData = {
        email: userData.email,
        first_name: userData.first_name,
        last_name: userData.last_name,
        study_name: userData.study_name,
        subject_id: event,
        user_type: "user"
      }

      Axios.post(`/users.json?auth=${this.idToken}`, subjData)
        .then(res => {
          this.authUser()
        })
        .catch(err => console.log(err))
    },
    authUser(){
      const userData = this.waitlist[this.userIdOnStack]
      AuthAxios.post('/signupNewUser?key=AIzaSyBgfyfLVpm5Xa9lTMesWPLA1Q3_2JJfOdI', {
        email: userData.email,
        password: userData.password,
        returnSecureToken: true
      })
      .then(err => {
        this.removeFromWaitlist()
      })
      .catch(err => console.log(err))
    },
    removeFromWaitlist() {
      Axios.delete(`/waitlist/${this.userIdOnStack}.json?auth=${this.idToken}`)
        .then(res => {
          this.$delete(this.waitlist, this.userIdOnStack)
          this.getWaitlistIndices()
          this.resetStackData()
        })
        .catch(err => console.log(err))
    }
  },
  components: {
    appEditRecord: EditRecord,
    appModal: Modal,
    appPagination: Pagination
  },
  created(){
    this.fetchWaitlist()
  }
}
</script>

<style scoped>
  .card {
    margin-top: 1rem;
    padding-top: 0.5rem;
    overflow-x: scroll;
    border-top: solid 3px rgb(44, 116, 250)
  }
  .table-hover tbody tr:hover td, .table-hover tbody tr:hover th {
    background-color: #a3cfec44;
  }
</style>
