import Vue from 'vue';
import Veulidate from 'vuelidate'
import App from './App.vue';
import axios from 'axios';
import router from './router';
import store from './store.js';

import BootstrapVue from 'bootstrap-vue';
import 'bootstrap/dist/css/bootstrap.css';
import 'bootstrap-vue/dist/bootstrap-vue.css'

import { library } from '@fortawesome/fontawesome-svg-core'
import { faHome, faFolderOpen, faUsers, faUtensils, faSignOutAlt, faPlus, faPencilAlt, faEdit, faDownload } from '@fortawesome/free-solid-svg-icons'
import { fab, faStackOverflow } from '@fortawesome/free-brands-svg-icons'
import { FontAwesomeIcon } from '@fortawesome/vue-fontawesome'

Vue.use(Veulidate)
Vue.use(BootstrapVue)
library.add(faHome, faFolderOpen, faUsers, faUtensils, faSignOutAlt, faPlus, faPencilAlt, faEdit, fab, faStackOverflow, faDownload)


Vue.component('font-awesome-icon', FontAwesomeIcon)

Vue.config.productionTip = false

axios.defaults.baseURL = 'https://diet-tracker-zch-331e8.firebaseio.com'; 
axios.defaults.headers.get['Accepts'] = 'application/json';

Vue.config.productionTip = false

new Vue({
  router,
  store,
  render: h => h(App),
}).$mount('#app')
